html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
    font-family: 'Roboto', sans-serif;
    line-height: normal;
}
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section { display: block }
a,input,textarea,button{ outline: none!important }
html{ height: 100% }
body { line-height: 1 }
ol, ul { list-style: none }
blockquote, q {	quotes: none }
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
a{
    color: #000;
    text-decoration: none;
}
*, *:before, *:after{
    box-sizing: border-box;
	outline: none!important
}

img,
.time_sort span,
.tab_panel span{
    -ms-user-select: none;
    -moz-user-select: none;
    -khtml-user-select: none;
    -webkit-user-select: none;
}
::-webkit-input-placeholder { color: #323232 }
::-moz-placeholder          { color: #323232 }
:-moz-placeholder           { color: #323232 }
:-ms-input-placeholder      { color: #323232 }
.text_overflow{
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
}
/* Start */
body {
    min-width: 320px;
}

textarea, input {
    resize: none;
    font-size: 16px;
    font-family: 'Roboto', sans-serif;
}

input, textarea {
    color: #323232;
    padding: 10px 12px;
    border: 1px solid #d7dee0;
    width: 100%;
    resize: none;
}

::-webkit-input-placeholder {
    color: #5e6973
}

::-moz-placeholder {
    color: #5e6973
}

:-moz-placeholder {
    color: #5e6973
}

:-ms-input-placeholder {
    color: #5e6973
}

/** Button **/
.blue_button {
    background: #65a3be;
    border: 1px solid transparent;
    color: #ffffff;
    font-size: 13px;
    border-radius: 3px;
    padding: 6px 12px;
    cursor: pointer;
}

.blue_button:hover {
    background: rgba(61, 144, 190, 0.93);
}

/* Container */
.vh_block, .vh_block_auth{
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.vh_block_auth{
    background: rgba(234,234,234,0.93);
}

.container {
    width: 100%;
    max-width: 1140px;
    display: flex;
    align-items: center;
    margin: 0 auto;
    padding: 0 20px;
}

.main {
    align-items: start;
    justify-content: space-between;
}

.main section:first-child {
    width: 100%;
}

.main_right {
    margin-left: 30px;
    min-width: 280px;
    display: flex;
    flex-direction: column;
}

.status_p {
    color: #548eaa;
    text-align: right;
    margin-left: 10px;
}

.flex{
    display: flex;
    align-items: center;
}

/* Main media */
@media only screen and (max-width: 750px) {
    .main_right {
        display: none;
    }
}
.right_column_box{
    background: #f7f7f7;
    padding: 20px;
    color: #9e9ea8;
    border-radius: 10px;
    margin-bottom: 20px;
}
.right_column_box h3 {
    color: #505c66;
    text-transform: uppercase;
    font-size: 14px;
    font-weight: bold;
    padding-bottom: 10px;
    border-bottom: 1px solid #e2e8e9;
}

.sponsor_box{
    background: #ffffff;
    padding: 10px;
    border-radius: 5px;
    margin: 15px 0;
    font-size: 14px;
}
.sponsor_box p{
    color: #222;
}
.title_sponsor{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 5px;
}
.title_sponsor h5{
    color: #333333;
    font-weight: bold;
}
.title_sponsor img{
    margin: 0 0 5px 10px;
    border-radius: 3px;
    width: 32px;
    height: 32px;
}
.right_streams_list {
    color: #548eaa;
}

.right_streams_list li {
    padding: 10px 0;
    font-size: 14px;
    border-bottom: 1px solid #e2e8e9;
}
.right_column_calendar{
    padding: 20px 0;
}
.right_statistics_list li {
    margin-top: 15px;
    display: flex;
    justify-content: space-between;
    color: #515d66;
    align-items: center;
}
.right_statistics_list i {
    font-weight: bold;
    font-size: 14px;
}
.right_statistics_list span {
    color: #4d80aa;
}
.page_not_found img{
    width: 100%;
}
.header {
    width: 100%;
    display: flex;
    justify-content: space-between;
    border-bottom: 2px solid #d5dddf;
    align-items: center;
    padding: 15px 0;
    margin-bottom: 20px;
    background: #ffffff;
}

.header .container {
    justify-content: space-between;
}

.header_right {
    display: flex;
    align-items: center;
}

.header_right h1 {
    margin-right: 35px;
}

.header_logo {
    font-size: 28px;
    font-weight: bold;
    margin-right: 30px;
}

.header_navigation a {
    font-size: 15px;
    margin-right: 25px;
    color: #838a92;
}

.header_navigation a:hover {
    color: #548eaa;
}

.active_page {
    color: #000 !important;
}

.header_panel, .header_mobile_user {
    display: flex;
    align-items: center;
}

.h_mobile_user_photo{
    border-radius: 3px;
}

.search_icon{
    width: 24px;
    height: 24px;
}

.header_panel button {
    font-size: 13px;
    border-radius: 3px;
    padding: 6px 12px;
    cursor: pointer;
}

.sign_in_btn {
    margin: 0 10px;
    background: #ffffff;
    border: 1px solid #a4afba;
    color: #838a92;
}

.sign_in_btn:hover {
    color: #65a3be;
    border: 1px solid #65a3be;
}

/* MOBILE */
.header_mobile_user {
    cursor: pointer;
    display: none;
}

.header_mobile_user img {
    width: 25px;
}

/* AUTH USER*/
.h_auth_user{
    margin-left: 10px;
}

.h_user_photo{
    width: 27px;
    height: 27px;
}

.h_user_photo img{
    border-radius: 3px;
    width: 100%;
}

/* MEDIA */
@media screen and (max-width: 750px) {
    .header_panel {
        display: none;
    }

    .header_mobile_user {
        display: flex;
    }

    .header_logo {
        margin-right: 0;
    }

    .mobile_burger {
        display: block !important;
    }

    .header_navigation {
        display: none;
    }
}

.mobile_burger{
    display: none;
}

.mobile_burger_title{
    text-align: center;
    margin: 10px 0 5px 0;

}

.burger_search{
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.menu-item a {
    display: block;
    padding: 20px 15px;
    color: inherit;
    text-decoration: none;
    border-bottom: 1px solid #ddd;
}

.menu-item a:hover {
    color: inherit;
    text-decoration: none;
    background-color: #eee;
}



footer {
    display: flex;
    flex-direction: column;
}
/* Information */
.footer_information {
    display: flex;
    padding: 20px 0;
    background: #f7f7f7;
}
.footer_information .container{
    justify-content: space-between;
    align-items: flex-start;
}
.footer_half{
    display: flex;
    width: 50%;
}
.footer_column {
    width: 100%;
    display: flex;
    flex-direction: column;
    margin-right: 20px;
}

.footer_column a, .footer_column ol li{
    font-size: 13px;
    margin-top: 10px;
    color: #548eaa;
}
.footer_column h3 {
    font-size: 15px;
    color: #464646;
    padding-bottom: 15px;
    border-bottom: 1px solid #d5dddf;
}

/* bottom footer */
.footer_bottom{
    background: #edeeef;
    padding: 20px 0;
    font-size: 13px;
}
.footer_bottom .container{
    justify-content: space-between;
}
.footer_bottom span{
    color: #77a8d0;
}
@media screen and (max-width: 630px){
    .footer_information .container{
        flex-direction: column;
        align-items: center;
    }
    .footer_half{
        width: 100%;
        margin-bottom: 20px;
    }
    .footer_column{
        text-align: center;
    }
    .footer_bottom .container{
        flex-direction: column-reverse;
    }
    .footer_bottom span {
        margin-bottom: 10px;
    }
}

@media screen and (max-width: 420px) {
    .footer_half{
        flex-direction: column;
    }
    .footer_column{
        margin: 15px 0 10px 0!important;
    }
}
.select_stream {
    width: 100%;
    max-width: 300px;
    margin-bottom: 10px;
}

.tab_panel {
    display: flex;
    margin-bottom: 20px;
    color: #6f7577;
    border-bottom: 1px solid #d5dddf;
    font-size: 14px;
    font-weight: bold;
    text-transform: uppercase;
}

.tab_panel span {
    padding: 20px 0;
    margin-right: 30px;
    cursor: pointer;
}

.tab_panel span:hover {
    color: #77a8d0;
}

.tab_panel_active {
    color: #77a8d0;
    border-bottom: 2px solid #77a8d0;
}

/* Time line*/
.time_sort {
    display: flex;
    border: 1px solid #d5dddf;
    align-content: flex-start;
    border-radius: 5px;
    width: 298px;
    align-items: center;
}

.time_sort span {
    padding: 14px 15px;
    color: #6c797f;
    cursor: pointer;
    border-radius: 5px;
    font-weight: bold;
    font-size: 14px;
    border: 2px solid transparent;
}

.time_sort span:hover {
    color: #77a8d0;
}

.active_time_btn {
    border: 2px solid #77a8d0 !important;
    color: #77a8d0;
}

.h_mobile_post {
    display: none;
}

/* pagination_panel */
.pagination_panel{
    width: -webkit-min-content;
    width: -moz-min-content;
    width: min-content;
    max-width: 298px;
    display: flex;
    border-radius: 5px;
    border: 1px solid #d5dddf;
    margin-bottom: 20px;
}

.pagination_panel li{
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 20px;
    height: 42px;
    width: 42px;
    color: #6c797f;
    cursor: pointer;
}

.pagination_panel li:hover{
    background: rgba(77,128,170,0.2);
}

.pagination_panel li:first-child{
    border-radius: 4px 0 0 4px;
}

.pagination_panel li:last-child {
    border-radius: 0 4px 4px 0;
}

.pagination_active{
    border: 1px solid #548eaa;
}

/* MEDIA Mobile Version*/
@media only screen and (max-width: 550px) {
    .h_mobile_post {
        display: block !important;
    }

    .h_full_post {
        display: none !important;
    }
}

@media screen and (max-width: 450px) {

    .tab_panel {
        justify-content: space-between;
    }

    .tab_panel span {
        margin-right: 0 !important;
    }

    .time_sort {
        width: 100%;
    }

    .time_sort span {
        padding: 14px 0;
        width: 25%;
        text-align: center;
    }

    .select_stream {
        max-width: none;
    }
}


.news_item {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    margin: 20px 0 40px 0;
}

/* Title */
.top_user {
    display: flex;
    align-items: center;
    margin-bottom: 10px;
    font-size: 14px;
}

.user_icon {
    width: 24px;
    height: 24px;
    margin-right: 5px;
    border-radius: 3px;
}

.user_name {
    color: #548eaa;
    margin-right: 10px;
}

.news_time {
    color: #5e6973;
}

.content_title {
    margin-bottom: 10px;
}

.title_news {
    font-size: 28px;
    color: #333333;
    font-weight: bold;
}

.title_news:hover {
    color: #77a8d0;
    cursor: pointer;
}

.title_h3 {
    font-size: 28px;
    color: #333333;
    font-weight: bold;
    margin-bottom: 10px;
}

.heading_news {
    display: flex;
    margin-bottom: 15px;
}

.heading_news p {
    font-size: 14px;
    color: #5e6973;
    margin-right: 5px;
}

.heading_news p:after {
    content: ',';
}

.heading_news p:last-child:after {
    content: " ";
}

.text_news {
    white-space: pre-wrap;
    font-size: 16px;
    margin-bottom: 20px;
}

.news_more {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 140px;
    border: 1px solid #77a8d0;
    padding: 0 13px;
    height: 40px;
    border-radius: 3px;
    font-size: 13px;
    margin-bottom: 20px;
    color: #77a8d0;
    cursor: pointer;
}

.news_more:hover {
    background: #77a8d0;
    color: #ffffff;
}

.news_stats {
    width: 100%;
    max-width: 340px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px;
    border: 2px solid #e5e9eb;
    border-radius: 5px;
}

.news_vote, .news_views, .news_favorite, .news_comments {
    display: flex;
    align-items: center;
    font-size: 14px;
    color: #548eaa;
}

.news_vote img {
    width: 10px;
}

.news_vote i {
    margin: 0 10px;
}

.stats_arrow_down {
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
}

.news_views img {
    width: 30px;
    margin-right: 5px;
}

.news_favorite img {
    width: 14px;
    margin-right: 8px;
}

.news_comments img {
    width: 20px;
    margin-right: 5px;
}

/* Mobile */
.m_post_news_item {
    border-bottom: 2px solid #e5e9eb;
    margin: 20px 0 !important;
    padding-bottom: 10px;
}

.m_post_title {
    font-size: 24px !important;
    margin: 10px 0;
}

.m_post_news_stats {
    margin-top: 10px;
    border: 0 !important;
}
.skeleton {
    -webkit-animation: animation-16jpnkj 1.2s ease-in-out 0s infinite normal none running;
            animation: animation-16jpnkj 1.2s ease-in-out 0s infinite normal none running;
    background: #eeeeee -webkit-linear-gradient(left, #eee, #f5f5f5, #eee) no-repeat;
    background: #eeeeee linear-gradient(90deg, #eee, #f5f5f5, #eee) no-repeat;
    background-size: 200px 100%;
    border-radius: 4px;
    width: 100%;
}

