.auth_container {
    width: 100%;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

.auth_form, .auth_bottom {
    width: 100%;
    max-width: 350px;
    border-radius: 5px;
    padding: 40px 40px 20px 40px;
    box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.1);
    background: #ffffff;
    position: relative;
}

.auth_signin {
