}

.auth_signup {
    min-height: 640px;
}

.auth_back {
    position: absolute;
    top: 15px;
    right: 15px;
    font-size: 14px;
    color: #333333;
}

.auth_back:hover {
    color: #558cb7;
}

.auth_bottom {
