.different_container {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    margin: 0 -10px 30px -10px;
}

.different_box {
    width: 33%;
    padding: 10px;
}

.different_block {
    border: 2px solid #d7dee0;
    height: 200px;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #a4afba;
    font-size: 18px;
    cursor: pointer;
    -webkit-transition: 0.5s;
    transition: 0.5s;
    position: relative;
}

.different_block:hover {
    background: rgba(215,222,224,0.5);
    color: #323232;
}

@media only screen and (max-width: 1100px) {
    .different_box {
        width: 50%;
    }
}

@media only screen and (max-width: 420px) {
    .different_box {
        width: 100%;
    }

    .different_block {
        height: 100px;
    }
}
